import React from "react";
import myPic from "../assets/MYPIC.jpg";

function About() {
  return (
    <section id="about">
      <div className="intro">
        <h1>
          <span id="greeting">Hey, I am a </span>
          <br />
          <span id="profession">Web Developer.</span>
        </h1>
        <p id="about-me">
          Welcome to my corner of the web! I'm Uma Mahesh, a passionate and
          creative web developer dedicated to crafting memorable digital
          experiences. With a keen eye for detail and a love for
          problem-solving, I specialize in turning ideas into reality through
          elegant and functional websites.
        </p>
      </div>
      <img src={myPic} alt="Uma Mahesh" className="my-pic" />
    </section>
  );
}

export default About;
