import projects from "../data/projects";

function ProjectItem({ name, description, link }) {
  return (
    <div className="project-item">
      <a href={link} target="_blank" rel="noreferrer" className="project-link">
        {name}
      </a>
      <p>{description}</p>
    </div>
  );
}

function Projects() {
  return (
    <section id="projects">
      <h2 className="heading">Projects</h2>
      <p className="note">
        Note: If you want to go to the github please click on the title of the
        project.
      </p>
      <div className="projects-container">
        {projects.map((project) => (
          <ProjectItem
            key={project.name}
            name={project.name}
            description={project.description}
            link={project.link}
          />
        ))}
      </div>
    </section>
  );
}

export default Projects;
