import React from "react";
import { useState } from "react";
import DevicesIcon from "@mui/icons-material/Devices";

function Header() {
  let [isMenuBarClicked, setIsMenuBarClicked] = useState(false);
  let [activeSection, setActiveSection] = useState("");

  function handleMenuBarClick() {
    setIsMenuBarClicked(!isMenuBarClicked);
  }

  function handleClick(e) {
    const sectionName = e.target.getAttribute("href").substring(1); // Extract section name from href
    setActiveSection(sectionName);
  }

  function handleMouseOver(e) {
    const sectionName = e.target.getAttribute("href").substring(1); // Extract section name from href
    setActiveSection(sectionName);
  }
  return (
    <header>
      <h2 id="logo">
        UMA MAHESH
        <DevicesIcon />
      </h2>
      <nav className="nav-bar">
        <div className={`nav-links ${isMenuBarClicked ? "active" : ""}`}>
          <a
            href="#about"
            onClick={handleClick}
            onMouseOver={handleMouseOver}
            className={`sectionLink ${
              activeSection === "about" ? "active" : ""
            }`}
          >
            ABOUT
          </a>
          <a
            href="#skills"
            onClick={handleClick}
            onMouseOver={handleMouseOver}
            className={`sectionLink ${
              activeSection === "skills" ? "active" : ""
            }`}
          >
            SKILLS
          </a>
          <a
            href="#projects"
            onClick={handleClick}
            onMouseOver={handleMouseOver}
            className={`sectionLink ${
              activeSection === "projects" ? "active" : ""
            }`}
          >
            PROJECTS
          </a>
          <a
            href="#contact"
            onClick={handleClick}
            onMouseOver={handleMouseOver}
            className={`sectionLink ${
              activeSection === "contact" ? "active" : ""
            }`}
          >
            CONTACT
          </a>
        </div>
        <div
          className={`menu-bar ${isMenuBarClicked ? "cross" : ""}`}
          onClick={handleMenuBarClick}
        >
          <div className="bar"></div>
          <div className="bar"></div>
          <div className="bar"></div>
        </div>
      </nav>
    </header>
  );
}

export default Header;
